// $bgColor  :  #959A93;
$bgColor  :  rgb(90,91,86);

$bannerColor  :  transparent;
$genericButtonColor  :  #A65751;
// $panelColor : #B1B7AE;
// $panelColor : rgb(97,98,93);
$panelColor : rgb(84,85,80);

// $panelFlyoutColor : rgba(82,104,112,1);
$panelFlyoutColor : rgba(84,106,114,1);

$birdRolloverColor : #6f8a91;

#sampleTable {
  position: relative;
  left: 0;
  width: 94%;
  min-width: 480px;
  margin: 0 auto 10px auto;
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
  empty-cells: show;
  border-collapse: collapse;
  border: none; }

/*#sampleTable.smallFont        { font-size : 10px; margin-bottom : 29px;}
#sampleTable.smallFont thead    { font-size : 9px; height : 18px;}
#sampleTable.smallFont td       { padding  :  1px 0 1px 0px; letter-spacing  :  1px; }
#sampleTable.smallFont td:first-child { padding  :  1px 0 1px 4px; letter-spacing  :  1px; }*/
#sampleTable td {
  height: 30px;
  padding: 2px 2px 2px 2px;
  border: solid 1px #000; }

#sampleTable thead {
  display: table-header-group;
  height: 27px; }

#sampleTable tbody {
  display: table-row-group; }

#sampleTable .cLfc, #sampleTable .cLfs {
  padding-top: 10px;
  font-weight: bold;
  text-align: left;
  border-right: hidden;
  border-left: hidden; }

#sampleTable .cLfs {
  text-align: left; }

#sampleTable .cLc {
  padding: 2px 0 2px 4px;
  border-right: hidden; }

#sampleTable .cLc, #sampleTable .cLs {
  text-align: left; }

#sampleTable .cLc, #sampleTable .cLFc {
  width: 200px; }

#sampleTable .cLs, #sampleTable .cLFs {
  width: auto; }

#sampleTable .cLs {
  padding: 2px 0 2px 0;
  font-style: italic; }

#sampleTable .fre {
  border-right: hidden; }

#sampleTable .cds {
  border-right: 3px double #000; }

#sampleTable .rds {
  border-bottom: 3px double #000; }

#sampleTable thead {
  font-size: 1.2rem;
  border: solid 1px #000; }

/*  there is no tfoot in #sampleTable  */
/*#sampleTable tfoot tr:first-child td  {
 	border-right   :  hidden;
 	border-left    :  hidden;
 	height         :  20px;
 }
 #sampleTable tfoot tr:last-child td   {  border : solid 1px #000;  height : 20px; }*/
#sampleTable th {
  height: 20px;
  padding: 0 2px;
  font-weight: bold;
  /*color  :  black;*/
  border: solid 1px #000;
  background-color: transparent; }

#sampleTable th:not(:first-child) {
  background-color: $panelColor; }

.flashDays {
  animation-name: flashDays;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0.3s; }

@keyframes flashDays {
  0%, 100% {
    color: black;
    transform: none; }
  50% {
    background-color: white;
    transform: scale(2); } }

// #sampleTable th:first-child {
//   border: solid 1px $panelColor;
//   border-right: solid 1px #000; }

#sampleTable td.familyHidden {
  border: solid 1px $panelColor;
  // border-top: solid 1px transparent;
   }

#sampleTable td.familyHidden.show {
  // border-top: solid 1px transparent;
  border-bottom: solid 1px #000; }

#sampleTable td.leftCheckBox {
  width: 0;
  border: solid 1px #000;
  border-top: transparent;
  border-bottom: transparent;
  border-left: transparent;
  transition: width 350ms; }

#sampleTable td.leftCheckBox.show {
  width: 30px;
  border: solid 1px #000;
  transition: width 350ms; }

.centerText {
  text-align: center; }

#sampleTable td.double-separator {
  border-right: 3px double #000; }

.noScientificNames .cLfc {
  letter-spacing: 0.1em; }

.noScientificNames .cLfs {
  visibility: hidden; }

#sampleTable.noScientificNames .cLc {
  padding: 2px 5px 2px 5px;
  letter-spacing: 0.1em; }

#sampleTable.noScientificNames .cLs {
  opacity: 0; }

#sampleTable.noItalics .cLs {
  font-style: normal; }

.showLineNumbers {
  position: relative; }

.showLineNumbers::before {
  content: '5';
  position: absolute;
  top: 6px;
  left: -24px;
  width: 17px;
  height: 17px;
  font-size: 1.1rem;
  color: #ddd;
  line-height: 14px;
  text-align: center;
  border: 1px solid black;
  border-radius: 50%;
  background: black; }

.showEndemics {
  position: relative; }

/*         order of the next three is important!          */
.showEndemics::before {
  content: 'e';
  position: absolute;
  top: 6px;
  left: -24px;
  width: 17px;
  height: 17px;
  font-size: 1.2rem;
  color: #ddd;
  line-height: 11px;
  text-align: center;
  border: 1px solid black;
  border-radius: 50%;
  background: black; }

#sampleTable.numDays0 .cLs {
  border-right: 1px solid #000; }

#sampleTable.numDays0 th {
  width: 0;
  border: 1px solid transparent; }

