.numDays3 td:nth-child(n+7) {
  display: none; }

.numDays2 td:nth-child(n+6) {
  display: none; }

.numDays1 td:nth-child(n+5) {
  display: none; }

.numDays0 td:nth-child(n+4) {
  display: none; }

#sampleTable th:first-child {
  border: solid 1px $panelColor;
  border-right: solid 1px #000; }

.numDays0 tr.topBorderDissappear {
  border-top : hidden; }


@media screen and (max-width: 590px) {
  #sampleTable {
    min-width: 400px;
    font-size: 1.1rem; }
  #sampleTable th {
    width: 21px; }
  .leftCheck::before {
    top: -7px;
    left: -37px;
    font-size: 48px; }
  .showLineNumbers::before {
    top: 3px;
    left: -23px;
    width: 16px;
    height: 16px;
    font-size: 9px;
    line-height: 14px; }
  .showEndemics::before {
    top: 3px;
    left: -23px;
    width: 16px;
    height: 16px;
    font-size: 9px;
    line-height: 12px; }
  .leftCheck.showEndemics::after {
    top: -25px;
    left: -37px;
    font-size: 48px; }
  .leftCheck.showLineNumbers::after {
    top: -25px;
    left: -37px;
    height: 0;
    font-size: 48px; } }
